.container {
    position: relative;
    display: flex;
    align-items: flex-end;
    width: 100%;
    height: 350px;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 10px;
    transform: scale(1);
    transition: all 0.3s;
    background-color: #FF6060;
}

.container:hover {
    transform: scale(1.03);
}

.container:hover .container__image {
    bottom: 0px;
}

.container__image {
    position: absolute;
    height: 110%;
    width: 100%;
    object-fit: cover;
    transition: all 0.3s ease-out;
    bottom: -5px;
    z-index: -1;
    filter: brightness(0.85);
}

.container__title {
    display: flex;
    align-items: flex-end;
    width: 70%;
    font-size: 18px;
    font-weight: 500;
    padding: 0 0 20px 20px;
    color: white;

}

/*mobile version */

@media screen and (max-width : 767px) {
    .container {
        height: 300px;
    }
}